import React, { useState } from "react";
import classes from "./UsernameForm.module.css";
import InvalidInput from "../InvalidInput/InvalidInput";
import Button from "../UI/Button/Button";
import Card from "../UI/Card/Card.js";

const UsernameForm = (props) => {
  const [enteredUsername, setEnteredUsername] = useState("");
  const [enteredAge, setEnteredAge] = useState("");
  const [invalidInput, setInvalidInput] = useState();

  const usernameChangedHandler = (e) => {
    setEnteredUsername(e.target.value);
  };
  const ageChangedHandler = (e) => {
    setEnteredAge(e.target.value);
  };

  const submitHandler = (e) => {
    e.preventDefault();
    if (enteredUsername.trim().length <= 0 || enteredAge.trim().length <= 0) {
      setInvalidInput({
        title: "Invalid input",
        message: "You cannot leave empty fields!",
      });
      return;
    }
    if (+enteredAge < 0) {
      setInvalidInput({
        title: "Invalid input",
        message: "You must enter a valid age (> 0)",
      });
      return;
    }

    const usernameData = {
      username: enteredUsername,
      age: enteredAge,
    };

    props.onSaveUsernameData(usernameData);

    setEnteredUsername("");
    setEnteredAge("");
  };

  const hideBackdrop = (e) => {
    setInvalidInput(null);
  };

  return (
    <div>
      {invalidInput && (
        <InvalidInput
          message={invalidInput.message}
          title={invalidInput.title}
          username={enteredUsername}
          age={enteredAge}
          onChangeInvalidInput={setInvalidInput}
          hideModal={hideBackdrop}
        ></InvalidInput>
      )}

      <Card className={classes.input}>
        <form onSubmit={submitHandler} className={classes.container}>
          <label htmlFor="username">Username</label>
          <input
            autoFocus
            value={enteredUsername}
            onChange={usernameChangedHandler}
            name="username"
            type="text"
          ></input>

          <label htmlFor="age">Age</label>
          <input
            value={enteredAge}
            onChange={ageChangedHandler}
            name="age"
            type="number"
          ></input>
          <div className={classes.button}>
            <Button type="submit">Submit</Button>
          </div>
        </form>
      </Card>
    </div>
  );
};

export default UsernameForm;
