import React from "react";
import classes from "./InvalidInput.module.css";
import Backdrop from "../Backdrop/Backdrop";
import Button from "../UI/Button/Button";

const InvalidInput = (props) => {
  return (
    <>
      <Backdrop hideBackdrop={props.hideModal}></Backdrop>

      <div className={classes["invalid-input"]}>
        <header className={classes["invalid-message"]}>
          <h2>{props.title}</h2>
        </header>
        <div className={classes.content}>
          <p>{props.message}</p>
        </div>

        <footer className={classes.actions}>
          <Button type="submit" onClick={props.hideModal}>
            Okay
          </Button>
        </footer>
      </div>
    </>
  );
};

export default InvalidInput;
