import React from "react";
import classes from "./Username.module.css";
import Card from "../UI/Card/Card";

const Username = (props) => {
  return (
    <Card className={classes.username}>
      <li>
        <h3>Name: {props.name}</h3>
        <h3>Age: {props.age}</h3>
      </li>
    </Card>
  );
};

export default Username;
